import axios from "axios";

const ApiBaseUrl = 'https://brobo.p4moviemaker.com/api/';

const token = sessionStorage.getItem('authtoken')
const Api = axios.create({
    baseURL: ApiBaseUrl,
    headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        Authorization: `Bearer ${token}`
    },
});

const ApiService = {
    getSettingData: (key) => Api.get('get-setting-data',{params:{key}}),
    getAllCoupons: () => Api.get('get-all-coupons'),
    getallBanners: ()=>  Api.get('get-all-banners'),
    signup: (userData)=> Api.post('auth/register',userData),
    login: (userData)=> Api.post('auth/login',userData),
    getallpopularservices:() => Api.get('get-popular-services'),
    getfastmovingitems:()=>Api.get('get-most-ordered-products')


};

export default ApiService;
