import React, { useState } from "react";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import "./login.css"; // Import CSS file for styling
import ApiService from "../../../Api/ApiController";
import Swal from "sweetalert2";
export default function Login() {
  const [value, setValue] = useState();
  const [mobilenumber,setmobilenumber] = useState('')
  const [password,setPassword] = useState('');
  const [Error, setError] = useState("");


const handleLogin = async (e) => {
  e.preventDefault();
  try {
    // Call the login API
    const response = await ApiService.login({
      mobile_number: mobilenumber,
      password,
      role_id: 2,
    });

    // Handle response
    console.log("Login response:", response.data);

    if (response.data.status === "success") {
      setmobilenumber("");
      setPassword("");
      setError(null);
      console.log("Login successful:", response.data);
      Swal.fire({
        icon: "success",
        title: "Login Successful",
        text: response.data.message,
      });
      sessionStorage.setItem('authtoken', response.data.data.auth_token); // Fix the typo here
      console.log(response.data.data.auth_token);
    } else {
      setError(response.data.message);
    }
  } catch (error) {
    console.error("Login failed:", error);
    // Handle the error here
    Swal.fire({
      icon: "error",
      title: "Login Failed",
      text: "An error occurred while logging in. Please try again later.",
    });
  }
};


  return (
    <div className="login-container">
      <section className="login-section">
        <div className="login-content">
          <div className="login-header">
            <h1>Login!</h1>
            <small>Welcome back, You've been missed!</small>
          </div>
          <form className="login-form" action="">
            <div className="form-group">
              <label htmlFor="mobile_number" className="form-label">
                Mobile Number:
              </label>
             <PhoneInput
                placeholder="Enter phone number"
                value={mobilenumber}
                onChange={setmobilenumber}
                defaultCountry="IN" // Set the default country to India
                className="form-input"
              />
            </div>
            <div className="form-group">
              <label htmlFor="password" className="form-label">
                Password:
              </label>
              <input
                type="password"
                id="password"
                name="password"
                value={password}
                onChange={(e)=>setPassword(e.target.value)}
                placeholder="Enter Password"
                className="form-input"
                required
              />
            </div>
            <small>Forget password?</small>
            {Error && <div className="error-message">{Error}</div>}
            <button onClick={handleLogin} className="">Submit</button>
            <div>
                <p>Don't have an account?<a href="">Sign up</a></p>
            </div>
          </form>
        </div>
      </section>
    </div>
  );
}
