import React, { useState, useEffect } from "react";
import "./header.css";
import brobo from "./brobo.png";
import { NavLink } from "react-router-dom";
export default function Header() {
  const [currentAddress, setCurrentAddress] = useState("");
  const [error, setError] = useState(null);
  const [editing, setEditing] = useState(false);

  useEffect(() => {
    const getLocation = () => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            getAddress(position.coords.latitude, position.coords.longitude);
          },
          (error) => {
            setError(error.message);
          }
        );
      } else {
        setError("Geolocation is not supported by this browser.");
      }
    };

    getLocation();
  }, []);

  const getAddress = (latitude, longitude) => {
    const geocoder = new window.google.maps.Geocoder();
    const latLng = { lat: latitude, lng: longitude };
    geocoder.geocode({ location: latLng }, (results, status) => {
      if (status === "OK") {
        if (results[0]) {
          setCurrentAddress(results[0].formatted_address);
        } else {
          console.error("No results found");
        }
      } else {
        console.error("Geocoder failed due to: " + status);
      }
    });
  };

  const handleEdit = () => {
    setEditing(true);
  };

  const handleSave = () => {
    // Handle saving the edited location
    // Here you may want to implement the logic to save the edited location
    setEditing(false);
  };

  const handleCancel = () => {
    // Handle canceling the editing
    setEditing(false);
  };

  const handleMapSelection = (selectedLocation) => {
    setCurrentAddress(selectedLocation);
    setEditing(false); // Close the modal after selecting the location
  };

  return (
    <div>
      <div className="header-container">
        <section>
          <nav className="nav-container">
            <div>
              <div className="user-icon">
                <button
                  className="side-nav-user-icon"
                  type="button"
                  data-bs-toggle="offcanvas"
                  data-bs-target="#offcanvasWithBothOptions"
                  aria-controls="offcanvasWithBothOptions"
                >
                  <i className="bx bxs-user-circle"></i>
                </button>
              </div>

              <div
                className="offcanvas offcanvas-start side-nav-bar"
                data-bs-scroll="true"
                tabindex="-1"
                id="offcanvasWithBothOptions"
                aria-labelledby="offcanvasWithBothOptionsLabel"
              >
                <div className="offcanvas-header">
                  <h5
                    className="offcanvas-title"
                    id="offcanvasWithBothOptionsLabel"
                  >
                    Hello Brobo
                  </h5>
                  <button
                    type="button"
                    className="btn-close"
                    data-bs-dismiss="offcanvas"
                    aria-label="Close"
                  ></button>
                </div>
                <div className="offcanvas-body ">
                  <div className="side-nav-navlinks">
                    <div>Personal Details</div>
                    <div>My Bookings</div>
                    <div>Delivery address</div>
                    <div>Wishlist</div>
                    <div>Pay for damage</div>
                    <div>Settings</div>
                    <div>Password</div>
                    <div>Help</div>
                    <div>FAQ</div>
                    <div>Share App</div>
                    <div>Referral code</div>
                    <div>Logout</div>
                    <div>About Brobo</div>
                    <NavLink to="/terms-condition">Terms and Condition</NavLink>
                    <NavLink to="/privacy-policy">Privacy Policy</NavLink>
                  </div>
                </div>
              </div>
            </div>
            <div className="brobo-img">
              <img src={brobo} alt="" />
            </div>
            <div className="address-container">
              <input
                type="text"
                value={currentAddress}
                readOnly
                className="address-input"
              />
              {editing ? (
                <div className="button-container">
                  <button onClick={handleSave} className="save-btn">
                    Save
                  </button>
                  <button onClick={handleCancel} className="cancel-btn">
                    Cancel
                  </button>
                </div>
              ) : (
                <button onClick={handleEdit} className="edit-btn">
                  <i className="bx bxs-pencil"></i>
                </button>
              )}
              {editing && (
                <LocationSelectorModal onMapSelection={handleMapSelection} />
              )}
            </div>
            <div className="icon-header">
            <div className="notification-icon">
              <i className="bx bxs-bell"></i>
            </div>
            <div className="cart-icon">
              <i className="bx bxs-cart"></i>
            </div>
            </div>
          </nav>
        </section>
      </div>
      <section className="secondary-nav">
        <div className="secondary-nav-container1">
          <div className="secondary-nav-container2">
            <ul>
              <li>
                <a href="#">Home</a>
              </li>
              <li>
                <a href="#">Sign in</a>
              </li>
              <li class="dropdown">
 Categories
  <div class="dropdown-content">
    <a href="#">Category 1</a>
    <div class="dropdown-subcontent">
      <a href="#">Subcategory 1.1</a>
      <a href="#">Subcategory 1.2</a>
    </div>
    <a href="#">Category 2</a>
    <div class="dropdown-subcontent">
      <a href="#">Subcategory 2.1</a>
      <a href="#">Subcategory 2.2</a>
    </div>
  </div>
</li>

              <li>
                <a href="#">About Us</a>
              </li>
              <li>
                <a href="#">Contact Us</a>
              </li>
            </ul>
          </div>
        </div>
        <div className="secondary-nav-container3">

        </div>
      </section>
     
    </div>
  );
}

const LocationSelectorModal = ({ onMapSelection }) => {
  useEffect(() => {
    const google = window.google;
    const mapOptions = {
      center: { lat: 0, lng: 0 }, // Default center
      zoom: 8, // Default zoom level
    };
    const mapElement = document.getElementById("map");
    const map = new google.maps.Map(mapElement, mapOptions);

    const searchBox = new google.maps.places.SearchBox(
      document.getElementById("search-input")
    );
    map.controls[google.maps.ControlPosition.TOP_LEFT].push(
      document.getElementById("search-input-container")
    );

    map.addListener("bounds_changed", () => {
      searchBox.setBounds(map.getBounds());
    });

    let markers = [];
    searchBox.addListener("places_changed", () => {
      const places = searchBox.getPlaces();

      if (places.length === 0) {
        return;
      }

      // Clear out the old markers
      markers.forEach((marker) => {
        marker.setMap(null);
      });
      markers = [];

      // For each place, get the icon, name and location.
      const bounds = new google.maps.LatLngBounds();
      places.forEach((place) => {
        if (!place.geometry) {
          console.log("Returned place contains no geometry");
          return;
        }
        const icon = {
          url: place.icon,
          size: new google.maps.Size(71, 71),
          origin: new google.maps.Point(0, 0),
          anchor: new google.maps.Point(17, 34),
          scaledSize: new google.maps.Size(25, 25),
        };

        // Create a marker for each place.
        markers.push(
          new google.maps.Marker({
            map,
            icon,
            title: place.name,
            position: place.geometry.location,
          })
        );

        if (place.geometry.viewport) {
          // Only geocodes have viewport.
          bounds.union(place.geometry.viewport);
        } else {
          bounds.extend(place.geometry.location);
        }
      });
      map.fitBounds(bounds);
    });

    google.maps.event.addListener(map, "click", (event) => {
      const selectedLocation = {
        lat: event.latLng.lat(),
        lng: event.latLng.lng(),
      };
      const geocoder = new google.maps.Geocoder();
      geocoder.geocode({ location: selectedLocation }, (results, status) => {
        if (status === "OK") {
          if (results[0]) {
            const formattedAddress = results[0].formatted_address;
            onMapSelection(formattedAddress);
          } else {
            console.error("No results found");
          }
        } else {
          console.error("Geocoder failed due to: " + status);
        }
      });
    });
  }, [onMapSelection]);

  return (
    <div className="modal1">
      <div className="modal-content1">
        <div id="search-input-container">
          <input
            id="search-input"
            type="text"
            placeholder="Search for location"
          />
          <button id="search-button">Search</button>
        </div>
        <div id="map" style={{ width: "100%", height: "300px" }}></div>
      </div>
    </div>
  );
};
