import React, { useEffect, useState } from "react";
import "./signup.css"; // Import your CSS file for styling
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import ApiService from "../../../Api/ApiController";
import Swal from "sweetalert2";
import { NavLink } from "react-router-dom";
export default function SignUp() {
  const [currentAddress, setCurrentAddress] = useState("");
  //   const [showLocationButton, setShowLocationButton] = useState(false);
  //   const [showLocationModal, setShowLocationModal] = useState(false);
  const [value, setValue] = useState("");
  const [name, setname] = useState("");
  const [mobilenumber, setmobilenumber] = useState("");
  const [password, setPassword] = useState("");
  const [referredCode, setReferredCode] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [Error, setError] = useState("");
  const [isChecked, setIsChecked] = useState(false);
  const handleSignUp = async (event) => {
    event.preventDefault();

    try {
        if (!isChecked) {
            setError("Please agree to the terms and conditions.");
            return;
          }
      // Call the sign up API
      if (password !== confirmPassword) {
        setError("Passwords do not match");
        return;
      }
      const response = await ApiService.signup({
        name,
        mobile_number: mobilenumber,
        password,
        address: currentAddress,
        referred_code: referredCode,
        fcm_token:'er',
      });

      // Handle response
      console.log("Sign up successful:", response.data);
      // Reset form fields after successful sign up
      if (response.data.status === "success") {
        // Reset form fields after successful sign up
        setname("");
        setmobilenumber("");
        setPassword("");
        setReferredCode("");
        setCurrentAddress("");
        setError(null);
        console.log("Sign up successful:", response.data);
        Swal.fire({
            icon: "success",
            title: "Sign Up Successful",
            text: response.data.message,
          });
      } else {
        setError(response.data.message); // Set the error message received from the API response
      }
    } catch (error) {
      console.error("Sign up failed:", error);
      setError(error.response.data.message);
    }
  };

  //   const getLocation = () => {
  //     if (navigator.geolocation) {
  //       navigator.geolocation.getCurrentPosition(
  //         (position) => {
  //           getAddress(position.coords.latitude, position.coords.longitude);
  //         },
  //         (error) => {
  //           console.error(error.message);
  //         }
  //       );
  //     } else {
  //       console.error("Geolocation is not supported by this browser.");
  //     }
  //   };

  //   const getAddress = (latitude, longitude) => {
  //     const geocoder = new window.google.maps.Geocoder();
  //     const latLng = { lat: latitude, lng: longitude };
  //     geocoder.geocode({ location: latLng }, (results, status) => {
  //       if (status === "OK") {
  //         if (results[0]) {
  //           setCurrentAddress(results[0].formatted_address);
  //         } else {
  //           console.error("No results found");
  //         }
  //       } else {
  //         console.error("Geocoder failed due to: " + status);
  //       }
  //     });
  //   };

  //   const handleAddressClick = () => {
  //     setShowLocationButton(true);
  //   };

  //   const handleSearchButtonClick = () => {
  //     setShowLocationModal(true);
  //   };

  //   const handleMapSelection = (selectedLocation) => {
  //     setCurrentAddress(selectedLocation);
  //     setShowLocationModal(false); // Close the location modal after selecting the address
  //     setShowLocationButton(false); // Hide the location buttons after selecting the address
  //   };
  //   const closelocationmodel = () => {
  //     setShowLocationModal(false);
  //     setShowLocationButton(false);
  //   };
  return (
    <div className="signup-container">
      <section className="signup-section">
        <div className="signup-form-container">
          <form className="signup-form" onSubmit={handleSignUp}>
            <div className="form-group">
              <label htmlFor="name" className="form-label">
                Name:
              </label>
              <input
                type="text"
                id="name"
                name="name"
                value={name}
                onChange={(e) => setname(e.target.value)}
                placeholder="Enter Your Name"
                className="form-input"
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="mobile_number" className="form-label">
                Mobile Number:
              </label>
              {/* <input type="tel" id="mobile_number" name="mobile_number" placeholder="Enter Mobile Number" className="form-input" required /> */}
              <PhoneInput
                placeholder="Enter phone number"
                value={mobilenumber}
                onChange={setmobilenumber}
                defaultCountry="IN" // Set the default country to India
                className="form-input"
              />
            </div>
            <div className="form-group">
              <label htmlFor="password" className="form-label">
                Password:
              </label>
              <input
                type="password"
                id="password"
                name="password"
                placeholder="Enter Password"
                className="form-input"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="confirm_password" className="form-label">
                Confirm Password:
              </label>
              <input
                type="password"
                id="confirm_password"
                name="confirm_password"
                className="form-input"
                placeholder="Confirm Password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                required
              />
            </div>
            {/* <div className="form-group address-fetch">
                            <label htmlFor="address" className="form-label">Address:</label>
                            <input type="text" id="address" value={currentAddress} name="address" className="form-input" readOnly onClick={handleAddressClick} />
                            {showLocationButton && (
                                <div className="location-buttons">
                                    <button
                                        type="button"
                                        className="btn btn-outline-dark "
                                        onClick={getLocation}
                                    >
                                        <i className='bx bx-current-location'></i> Current Location
                                    </button>
                                    <button onClick={closelocationmodel} type="button" className="btn btn-close "></button>
                                    <button
                                        type="button"
                                        className="btn btn-outline-dark"
                                        onClick={handleSearchButtonClick}
                                    >
                                        <i className='bx bx-search'></i> Search
                                    </button>
                                </div>
                            )}
                            {showLocationModal && <LocationSelectorModal onMapSelection={handleMapSelection} />}
                        </div> */}
            <div className="form-group">
              <label htmlFor="referred_code" className="form-label">
                Referred Code:
              </label>
              <input
                type="text"
                id="referred_code"
                name="referred_code"
                className="form-input"
                placeholder="Enter Referal Code(Optional)"
              />
            </div>
            <div className="form-group">
              <label>
                <input
                  type="checkbox"
                  checked={isChecked}
                  onChange={(e) => setIsChecked(e.target.checked)}
                />
                I agree to the <NavLink to='/terms-condition'>terms and conditions</NavLink> and <NavLink to="/privacy-policy">privacy policy</NavLink> of Brobo
              </label>
            </div>
            {Error && <div className="error-message">{Error}</div>}
            <button type="submit" className="submit-btn">
              Sign Up
            </button>
          </form>
        </div>
      </section>
    </div>
  );
}

